const { regClass, property } = Laya;
import GameControl from "./PhysicsGameMain"
import { PhysicsGameMainRTBase } from "./PhysicsGameMainRT.generated";


@regClass('Y5UkAabiTgWFZQnBL1EpEw')

/**
 * IDERuntime
 * IDEvarthis.tipLbllthis.scoreLbl
 * 
 */
export default class PhysicsGameMainRT extends PhysicsGameMainRTBase {
    /** */
    static instance: PhysicsGameMainRT;
    /** */
    private _score: number;
    /** */
    private _control: GameControl;

    constructor() {
        super();
        PhysicsGameMainRT.instance = this;
        //
        // Laya.MouseManager.multiTouchEnabled = false;
    }


    onEnable(): void {
        this._control = this.getComponent(GameControl);
        //
        this.tipLbll.on(Laya.Event.CLICK, this, this.onTipClick);
    }

    onTipClick(e: Laya.Event): void {
        this.tipLbll.visible = false;
        this._score = 0;
        this.scoreLbl.text = "";
        this._control.startGame();
    }

    /** */
    addScore(value: number = 1): void {
        this._score += value;
        this.scoreLbl.text = "" + this._score;
        //
        if (this._control.createBoxInterval > 600 && this._score % 20 == 0) this._control.createBoxInterval -= 20; 
    }

    /** */
    stopGame(): void {
        this.tipLbll.visible = true;
        this.tipLbll.text = "";
        this._control.stopGame();
    }
}