import { IndexRTBase } from "./IndexRT.generated";

const { regClass, property } = Laya;

@regClass('wtSVevJMRPCBIPp2HPhqpw')
export default class IndexRT extends IndexRTBase {

    constructor() { super(); }

    onEnable(): void {

        console.log("IndexRT onEnable")
        //ui
        this.uiBtn.on(Laya.Event.CLICK, this, () => {
            //UI
            console.log("uiBtn");
            Laya.Scene.open("scenes/UiMain.ls");//Laya.Scene.open("./Scenes/PhysicsGameMain.ls");
        });

        //
        this.phyBtn.on(Laya.Event.CLICK, this, () => {
            //
            console.log("phyBtn");
            Laya.Scene.open("scenes/PhysicsGameMain.ls");//Laya.Scene.open("./Scenes/PhysicsGameMain.ls");
        });

        //3D
        this.d3Btn.on(Laya.Event.CLICK, this, () => {
            //3D
            console.log("d3Btn");
            Laya.Scene.open("scenes/D3Main.ls");//Laya.Scene.open("./Scenes/D3Main.ls");
        });

        
    }

    onDestroy(): void {

        // if( this.uiBtn.hasListener( Laya.Event.CLICK ) )
        //     console.log("uiBtn");

    }
}