const { regClass, property } = Laya;
import PhysicsGameMainRT from "../scence/physicsDemo/PhysicsGameMainRT";
/**
 * 
 */
@regClass('3L23NCFLSAieUApqTrVoCQ')
export default class DropBox extends Laya.Script {
    @property({ type: "number" })
    /** */
    level: number = 1;
    /** */
    private _text: Laya.Text;
    /** */
    private _rig: Laya.RigidBody

    private _itemId: number;
    public get itemId(): number {
        return this._itemId;
    }
    public set itemId(value: number) {
        this._itemId = value;
        (this.owner as Laya.Image).skin = "resources/UI/images/bag/" + value + ".png";
    }

    constructor() { super(); }
    onEnable(): void {
        /** */
        this._rig = this.owner.getComponent(Laya.RigidBody);
        this.level = Math.round(Math.random() * 5) + 1;
        // this._text = this.owner.getChildByName("levelTxt") as Laya.Text;
        // this._text.text = this.level + "";
    }

    onUpdate(): void {
        //
        // (this.owner as Laya.Sprite).rotation++;
    }

    onCollisionEnter(collision: Laya.Collision): void {
        console.log("collision: ", collision);
    }

    onTriggerEnter(other: any, self: any, contact: any): void {
        var owner: Laya.Sprite = this.owner as Laya.Sprite;
        console.log("owener: ", owner);
        if (other.label === "buttle") {
            //
            // if (this.level > 1) {
            //     this.level--;
            //     this._text.text = (this.level + "");
            //     owner.getComponent(Laya.RigidBody).setVelocity({ x: 0, y: -10 });
            //     Laya.SoundManager.playSound("resources/sound/hit.wav");
            // } else {
                if (owner.parent) {
                    let effect: Laya.Animation = Laya.Pool.getItemByCreateFun("effect", this.createEffect, this);
                    effect.pos(owner.x, owner.y);
                    owner.parent.addChild(effect);
                    effect.play(0, true);
                    owner.removeSelf();
                    Laya.SoundManager.playSound("resources/sound/destroy.wav");
                }
            // }
            
            // PhysicsGameMainRT.instance.addScore(1);
        }
        //  else if (other.label === "ground") {
        //     //
        //     owner.removeSelf();
        //     PhysicsGameMainRT.instance.stopGame();
        // }
    }

    /** */
    createEffect(): Laya.Animation {
        let ani: Laya.Animation = new Laya.Animation();
        ani.loadAnimation("resources/ani/TestAni.ani");
        ani.on(Laya.Event.COMPLETE, null, recover);
        function recover(): void {
            ani.removeSelf();
            Laya.Pool.recover("effect", ani);
        }
        return ani;
    }

    onDisable(): void {
        //
        Laya.Pool.recover("dropBox", this.owner);
    }
}