    /** 
     * @param direct 
     */
    public set roleDirection(direct: number) {
        this._roleDirection = direct;
        if(direct == -4){
            return;
        }
        if(direct>=2 && direct <= 4){
            this.directStr = "right";
        }else if(direct>=6 && direct <= 8){
            this.directStr = "left";
        }
        if(this.lastDirectStr != this.directStr){
            this.heroBox.scaleX = this.directStr == "left"? 1 : -1;
            this.lastDirectStr = this.directStr;
        }

    }
    /** 
     * @param direct 
     */
    public set myRoleDirection(direct: number) {
       
        if(this.roleDirection != direct){
            this.roleDirection = direct;
            for(var i=1;i<9;i++){
            
                (this as any)["img_d_"+i].visible = i==direct;
    
            }
            this.img_gj.rotation = 45 * (direct - 1);
        }

    }




