    private onRoll(newP:any[]){
        if(!this.inGround)
            Laya.Tween.to(this, { x:newP[0], y: newP[1] }, 330);
    }
    private onHuaComplate(){
        this.huaSpineSk.stop();
        this.huaSpineSk.visible = false;
    }


    private onRealAttack(skillItem:SkillItem, targetId:number = -1){
        console.log("onRealAttack", targetId);
        if(targetId > 0){
            GameManager.instance.onSendAttack(targetId, 1, skillItem.id);//,1=2=
        }else{
            var defHero = GameManager.instance.getAttackTarget(skillItem);
            if(defHero && defHero.roomPlayerDto.hp>0){
                GameManager.instance.onSendAttack(defHero.roomPlayerDto.id, 1, skillItem.id);//,1=2=
            }
        }
            
    }

