    // 
    private showDamageLabel(skillId:number, damages: number[], attackPush:AttackPush): void {
        if(damages.length > 1){
            var skillItem:SkillItem = SheetDataManager.intance.m_dicSkills.get(skillId);
            var section =  skillItem.section;
            var resultArray = section;//JSON.parse(section as any);

            for(var i = 0; i< damages.length; i++){
                var damage = damages[i];                
                this.showDamageLabelDetailDelay(damage, resultArray[i][1], attackPush);
            }
        }else if(damages.length == 1){
            this.showDamageLabelDetailDelay(damages[0], 100, attackPush);
        }else{
            this.showDamageLabelDetailDelay(0, 100, attackPush);
        }
        // this.img_hp_bg.blendMode = "lighter";
        var that = this;
        if(attackPush.allAtkValue != 0){
            // laya.timer

            var duration = 100 * 2; // 
            this.originalBlendMode = this.img_hp_bg.blendMode; // blendMode
            this.flashCount = 0; // 
            Laya.timer.loop(duration, this, this.flashBlood);
        }

    }

    private originalBlendMode:string; // blendMode
    private flashCount:number = 0; // 

    private flashBlood(){
        var numFlashes = 2; // 
        if (this.flashCount < numFlashes) {
            if (this.img_hp_bg.blendMode === this.originalBlendMode) {
                this.img_hp_bg.blendMode = "lighter";
            } else {
                this.img_hp_bg.blendMode = this.originalBlendMode;
                this.flashCount++;
            }
        } else {
            this.img_hp_bg.blendMode = this.originalBlendMode;
            // 
            Laya.timer.clear(this, this.flashBlood);
        }

    }
    // 
    private showDamageLabelDetailDelay(damage: number, delay:number, attackPush:AttackPush): void {
        Laya.timer.once(delay, this, this.showDamageLabelDetail, [damage, attackPush], false);

    }
    // 
    private showDamageLabelDetail(damage: number, attackPush:AttackPush): void {
        var _damageLabel:DamageNumber = Laya.Pool.getItemByCreateFun("damageNumber", GameManager.instance.damageNumPrefab.create, GameManager.instance.damageNumPrefab);

        // // 
        // if(damage!=0){
        //     var randomX = Tool.getRandInRound(-30, 30);
            _damageLabel.init(damage, attackPush);// 
        // }else{
        //     _damageLabel.init(`${damage}`, attackPush, -30, -180);// 
        // } 

        this.addChild(_damageLabel);        
    }

    lastX:number = -1;
    lastY:number = -1;
    lastRoleDirection1: number = -1;
    lastSpeed1: number = -1;
    lastArea:number = -1;

    onSendMoveData():void{
        if (this._isDead) {//
            return;
        }
        var speed:number = this.speed;
        var dire:number = this.roleDirection;
        var pX:number = this.x;
        var pY:number = this.y;
        ////console.log("RoleId: " + this.owner.roomPlayerDto.id + ", Role onSetMoveData: speed:" + speed + ", dire: " + dire);
        // this.roleDirection = dire;
        if( Math.abs(pX - this.lastX) > 2 || Math.abs(pY - this.lastY) > 2 || this.lastRoleDirection1 != dire || this.lastSpeed1 != speed || this.lastArea != this.currArea){
            this.pos(pX, pY);
            this.lastX = pX;
            this.lastY = pY;
            this.lastRoleDirection1 = dire;
            this.lastSpeed1 = speed;
            this.lastArea = this.currArea;
            var timeStamp = GlobalDataManager.instance.m_serverTime;
            this.timeStamp = timeStamp;
            GameManager.instance.onSendMove(timeStamp, this.roomPlayerDto.id, speed, dire, pX, pY, this.currArea);
            
        }
        // this.owner.pos(x, y);
    }


    beiTui(sourceId:number) {
        // return;
        // var sourceId:number = this.owner.roomPlayerDto.id;
        var targetId:number = this.roomPlayerDto.id;
        var targetX:number = this.x;
        var targetY:number = this.y;

        if( Math.abs(targetX - this.lastX) > 2 || Math.abs(targetY - this.lastY) > 2){
            // this.pos(pX, pY);
            this.lastX = targetX;
            this.lastY = targetY;

            var timeStamp = GlobalDataManager.instance.m_serverTime;
            this.timeStamp = timeStamp;
            GameManager.instance.onSendTargetMove(timeStamp, sourceId, targetId, targetX, targetY);
            
        }
    }



    

    // lastX:number;
    // lastY:number;
    /** */
    onPushMoveData(time:number, speed:number, dire:number, x:number, y:number, area:number):void{
        ////console.log("RoleId: " + this.owner.roomPlayerDto.id + ", Role onSetMoveData: speed:" + speed + ", dire: " + dire);
        this.roleDirection = dire;
        if(this.speed != speed){
            this.speed = speed;
            if(speed == 0){
                this.idle();
            }else{
                this.walk();
            }
        }
        if(this.currArea != area){
            this.currArea = area;
            
        }

        //   

        if(time<=this.timeStamp){
            console.log("" + time + " < " + this.timeStamp );
            return;
        }
            
        if( Math.abs(x - this.lastX) > 1 || Math.abs(y - this.lastY) > 1){
            this.pos(x, y);
            this.lastX = x;
            this.lastY = y;
        }
        // this.owner.pos(x, y);
    }
    /** */
    onPushTargetMoveData(time:number, x:number, y:number):void{
        ////console.log("RoleId: " + this.owner.roomPlayerDto.id + ", Role onSetMoveData: speed:" + speed + ", dire: " + dire);
        //   

        if(time<=this.timeStamp){
            console.log("" + time + " < " + this.timeStamp );
            return;
        }
            
        if( Math.abs(x - this.lastX) > 2 || Math.abs(y - this.lastY) > 2){
            this.pos(x, y);
            this.lastX = x;
            this.lastY = y;
        }
        // this.owner.pos(x, y);
    }

