    /**spine */
    createBirthSpine(): void {
        this.heroSk.visible = false;
        this.tf_name.visible = false;
        this.box_blood.visible = false;
        //spine
        if(!this.chushengBackSpineSk){
            this.chushengBackSpineSk = new Laya.SpineSkeleton();
            this.spineNodeBack.addChild(this.chushengBackSpineSk);
        }

        //spine
        if(!this.chushengFrontSpineSk){
            this.chushengFrontSpineSk = new Laya.SpineSkeleton();
            this.spineNodeFront.addChild(this.chushengFrontSpineSk);
        }
        
        this.chushengBackSpineSk.on(Laya.Event.READY, this, this.onBirthReady);
        //spinespineIDEspine
        // this.spineTemplet = new SpineTemplet();//Laya.SpineVersion.v3_8);
        // this.spineTemplet.on(Event.STOPPED, this, this.spineParseComplete)
        // this.spineTemplet.loadAni("role/sk/spineboy-pma.skel");
        // var birthSk = "gameIcon/effect/ruchang/effect" + this.getRandomAnimationType() + ".skel";
        var templet: Laya.SpineTemplet = GameManager.instance.ruchangEffects[this.getRandomAnimationType()];
        // Laya.loader.load(birthSk,Laya.Loader.SPINE).then((templet: Laya.SpineTemplet) => {
            if(this.destroyed){
                return;
            }
            //spine
            // this.sk = new SpineSkeleton();
            this.chushengBackSpineSk.animationName = "animation2";
            this.chushengBackSpineSk.templet = templet;
            // this.sk.templet.on(Laya.Event.COMPLETE, this, this.spineParseComplete);
            
            this.chushengFrontSpineSk.animationName = "animation1";
            this.chushengFrontSpineSk.templet = templet;
            // this.sk.templet.on(Laya.Event.COMPLETE, this, this.spineParseComplete);
            

        // });        
        
    }
    //  0  2 
    private getRandomAnimationType(): number {
        return Math.floor(Math.random() * 3);
    }

    onBirthReady(){
        Laya.timer.once(500, this, this.onPlayBirth1);
    }
    

    onPlayBirth1(){
        this.isLockAni = true;
        this.heroSk.visible = true;

        this.playChuShengAnimation(() => {
            // 
            this.isLockAni = false;
            this.aniChange = true;
            // this.onPlayBirth2();
        });

        // this.adjustLayers(this.sk_chusheng, 1);
        this.chushengBackSpineSk.visible = true;
        this.chushengBackSpineSk.play("animation2", false);
        this.chushengFrontSpineSk.visible = true;
        this.chushengFrontSpineSk.play("animation1", false);

        Laya.timer.once(1300, this, this.onPlayBirthEnd);
    }

