    onPlayBirthEnd(){
        // this.chushengBackSpineSk.visible = false;
        // this.chushengFrontSpineSk.visible = false;
        if(this.chushengBackSpineSk){
            this.chushengBackSpineSk.off(Laya.Event.READY, this, this.onPlayBirth1);
            this.chushengBackSpineSk.destroy();
            this.chushengBackSpineSk.removeSelf();

        }
        if(this.chushengFrontSpineSk){
            this.chushengFrontSpineSk.destroy();
            this.chushengFrontSpineSk.removeSelf();
        }
        

        this.box_d.visible = true;
        // this.heroSk.visible = false;
        this.tf_name.visible = true;
        this.box_blood.visible = true;
    }


