import { TreeListRTBase } from "./TreeListRT.generated";

const { regClass, property } = Laya;

@regClass('GJWtWFwsQoqNpjA1k42SpQ')
export default class TreeListRT extends TreeListRTBase {


    onEnable(): void {
        //treexml
        this.tree1.xml = this.getTreeData(true);
        this.tree2.xml = this.getTreeData(false);
    }

    getTreeData(_static: boolean): any {
        //
        let treeData: string = "<data>";
        if (_static) {//
            //elsexml
            treeData +=
                "<dir itemLabel='' isOpen='false'>" +
                "<file itemLabel='1 '/>" +
                "<file itemLabel='2 '/>" +
                "<file itemLabel='3 '/>" +
                "<file itemLabel='4 '/>" +
                "<file itemLabel='5 '/>" +
                "</dir>" +
                "<dir itemLabel='' isOpen='true'>" +
                "<file itemLabel='1 '/>" +
                "<file itemLabel='2 '/>" +
                "<file itemLabel='3 '/>" +
                "</dir>" +
                "<dir itemLabel='' isOpen='false'>" +
                "<file itemLabel='1 '/>" +
                "<file itemLabel='2 '/>" +
                "<file itemLabel='3 '/>" +
                "<file itemLabel='4 '/>" +
                "<file itemLabel='5 '/>" +
                "</dir>" +
                "<file itemLabel='1 '/>" +
                "<file itemLabel='2 '/>";//
        } else {//
            //
            for (let i: number = 0; i < 5; i++) {
                //item,titlelabelname
                treeData += "<item title='" + (i + 1) + "' isOpen='true'>";
                for (let j: number = 0; j < 5; j++) {
                    //(subpagetitlelabelname
                    treeData += "<subpage title='" + (j + 1) + "' />";
                }
                //
                treeData += "</item>";
            }
        }
        //data
        treeData += "</data>"
        //xml
        return new Laya.XML(treeData);;
    }
}