
import { Dictionary } from "../../../util/Dictionary";
import { EthereumManager } from "../EthereumManager";

export class ETHUtils {

    public static getInviteUrl(){
        return window.location.origin + '?invite=' + this.encode(EthereumManager.instance.walletAddress);
    }
    //
    public static encode (str: string) {  
        //36
        var key = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ";
        var len = key.length;  //
        var a = key.split("");  //
        var s = "",b, b1, b2, b3;  //
        for (var i = 0; i <str.length; i ++) {  //
            b = str.charCodeAt(i);  //Unicode
            b1 = b % len;  //Unicode
            b = (b - b1) / len;  //
            b2 = b % len;  //
            b = (b - b2) / len;  //
            b3 = b % len;  //
            s += a[b3] + a[b2] + a[b1];  //
        }
        return window.btoa(s);  //
    } 
    //
    public static decode (str: string) {
        str = window.atob(str);
        //36
        var key = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ";
        var len = key.length;  //
        var b, b1, b2, b3, d = 0, s;  //
        s = new Array(Math.floor(str.length / 3));  //
        b = s.length;  //
        for (var i = 0; i < b; i ++) {  //
            b1 = key.indexOf(str.charAt(d));  //
            d ++;
            b2 = key.indexOf(str.charAt(d));  //
            d ++;
            b3 = key.indexOf(str.charAt(d));  //
            d ++;
            s[i] = b1 * len * len + b2 * len + b3  //Unicode
        }
        
        b = decodeCharCode(s.join(',') as any);
        // b = String.fromCharCode(s.join(',') as any);
        // b = eval("String.fromCharCode(" + s.join(',') + ")");
        return b ;  //
    }

    //url
    public static getQueryString(name: string):any
    {
        var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
        var r = window.location.search.substr(1).match(reg);
        if(r!=null)
            return r[2];     //jsunescape
        return '';
    }

    public static getInviteWalletAddress(){
        let url = this.getQueryString('invite');
        if(url){
            url = this.decode(url);
        }
        return url;
    }

    public static toWei(value: number,decimals: string){
        let num = value*10**parseInt(decimals);
        let tokens=(window as any)['web3'].utils.toBN("0x" + num.toString(16));
        return tokens.toString();
    }
    

    public static fromWei(value: string | any[],decimals: number,point=0){
        if(!value){
            return 0;
        }
       let len = value.length - decimals + point;
       if(len <= 0){
           return 0;
       }
       let temp = value.toString().slice(0,len);
       if(point){
           let tt = temp.length -point
           if( tt<=0){
                let ttt = '0.';
                for (let index = 0; index < Math.abs(tt); index++) {
                    ttt += '0'
                }
                temp = ttt + temp;
           }
           else
           {
               temp = temp.slice(0,temp.length -point) + '.' + temp.slice(temp.length-point)
           }
       }
       return temp;
    }

    public static deepClone(obj:any):Dictionary{
        let temp:any = new Dictionary();
        for(let k in obj){
            if(typeof obj[k]=='function'){
               //
               temp[k] = obj[k]
            }else{
                temp[k] = JSON.parse(JSON.stringify(obj[k]))
         }
         }
         return temp;
    }

}

