const { regClass } = Laya;
import { HeroVo } from "../../global/contract/vo/HeroVo";
import { EnumHero } from "../../global/data/enumData/EnumHero";
import { HeroSkBase } from "./HeroSk.generated";

@regClass('kS5K53GHRWOdgfXoo6VH1A')
export class HeroSk extends HeroSkBase {

    public heroVo: HeroVo;
    
    private isAddEvent:boolean = false;
    public isReady:boolean = false;
    /**  */
    private skinIndex: number = 0;
    /** spine  */
    private spineAniIndex: number = 0;
    /** spine */
    private skeletonNode: Laya.Sprite;
    // /**spine */
    // private spineNode: Laya.Sprite;
    /** spine */
    private spineAniNum: number;
        /**  */
        private skinArray: Array<string> = ["goblin", "goblingirl"];


    public constructor() {
        super();
    }

    private addEvent(){
        if(!this.isAddEvent){
            // this.img_chanchu.on(Laya.Event.CLICK, this, this.onDigGet);
            // this.on(Laya.Event.CLICK,this, this.toDetail)
            this.isAddEvent = true;
            this.on(Laya.Event.CLICK, this, this.changeAni);
            this.sk.on(Laya.Event.READY, this, this.onSkReady);
            // (this.sk as any)["_complete"] = new Laya.Handler(this,this.spineParseComplete)
        }

    }
    private onSkReady(){
        this.isReady = true;

    }


    public set dataSource(value: any) {
        super.dataSource = value;
        this.heroVo = value as HeroVo;
        // if(!this.heroVo ) {
        //     return
        // }
        this.addEvent();
        this.createSpine();
    }






        /**spine */
        createSpine(): void {
            
            //spinespineIDEspine
            // this.spineTemplet = new SpineTemplet();//Laya.SpineVersion.v3_8);
            // this.spineTemplet.on(Event.COMPLETE, this, this.spineParseComplete)
            // this.spineTemplet.loadAni("role/sk/spineboy-pma.skel");
            Laya.loader.load(this.heroVo.sk,Laya.Loader.SPINE).then((templet: Laya.SpineTemplet) => {
                if(this.destroyed){
                    return;
                }
                //spine
                // this.sk = new SpineSkeleton();
                this.sk.templet = templet;
                // this.sk.templet.on(Laya.Event.COMPLETE, this, this.spineParseComplete);
                this.sk.animationName = EnumHero.ANI_daiji;
                // this.spineNode.addChild(this.sk);
                // this.sk.scale(0.3, 0.3);
                // this.sk.pos(220, 390);
    
                //spine
                // this.spineAniNum = this.sk.getAnimNum();
                this.spineAniNum = EnumHero.ANIS.length;
                //
                
                //
                
                // this.on(Laya.Event.CLICK, this, this.changeSkin);
            });        
            
        }
    
        // /** spine */
        // spineParseComplete(): void {
        //     this.sk.play(EnumHero.ANI_daiji, true);
        // }
    
        /**  */
        changeAni(): void {
            if(!this.isReady)
                return;
            //spine
            // ++this.spineAniIndex >= this.spineAniNum && (this.spineAniIndex = 5);
            
            this.spineAniIndex++;
            if(this.spineAniIndex >= this.spineAniNum)
            this.spineAniIndex = 0;
            var aniName:string = EnumHero.ANIS[this.spineAniIndex];
            //
            this.sk.play(aniName, true, true);
            
        }
    
    
    
        /**  */
        changeSkin(): void {
            //spine,showSkinByIndex(index)
            this.sk.showSkinByName(this.skinArray[this.skinIndex]);
            //indexskinIndex
            // this.skeletonAni.showSkinByIndex(index);
    
            //
            ++this.skinIndex >= this.skinArray.length && (this.skinIndex = 0);
        }

        onDisable(): void {
            this.sk.off(Laya.Event.READY, this, this.onSkReady);
        }




    
    // toDetail() {
    //     if(this.heroVo.id){
    //         ModuleManager.intance.openModule(ModuleName.HeroDetailDialog,this.heroVo) 
    //     }
    // }

}